import React, { Component } from 'react';
import { Cache } from 'aws-amplify';
import { connect } from 'react-redux';
import '../../css/dashboard.css';
import { Container, Navbar } from 'react-bootstrap';
import logo from '../../assets/logo.png';

import AdminMenus from './AdminMenus';
import PortfolioManagement from './management/PortfolioManagement';

class Dashboard extends Component {
  componentDidMount() {
    const isLoggedIn = this.props.users.isLoggedIn;
    const apiToken = Cache.getItem('apiToken');
    if (!apiToken) {
      this.props.history.push('/home');
    }
  }

  switchContent = () => {
    const pathname = this.props.location.pathname;

    switch (pathname) {
      case '/dashboard/portfolio':
        return <PortfolioManagement />;
      default: 
        return '';
    }
  }

  render() {
    return (
      <React.Fragment>
        <Navbar bg="dark" id="admin-header">
          <Navbar.Brand href="#home">
            <img  src={logo} width="100" height="100" alt="gianne-amparo-illustrations" />
          </Navbar.Brand> 
          Administrator
        </Navbar>
        <Container>
          <AdminMenus />
          <Container id="dashboard-main">
            { this.switchContent() }
          </Container>
        </Container>
      </React.Fragment>
    )
  }
}

const mapStateToProps = (state) => ({
  users: state.users
});

const mapDispatchToProps = (dispatch) => {
  return {

  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard)