import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { Row, Col } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faImages, faSignOutAlt, faUserTie, faCog } from '@fortawesome/free-solid-svg-icons';
import { logoutUser } from '../../store/actions/usersActions';

class AdminMenus extends Component {
  render() {
    return (
      <React.Fragment>
        <div class="sidenav">
          <Link href="#" onClick={(e) => {
            e.preventDefault();
            this.props.history.push('/dashboard/portfolio');
          }}>
            <FontAwesomeIcon icon={faImages} /> Manage Portfolios
          </Link>
          <Link href="#" onClick={(e) => {
            e.preventDefault();
            this.props.history.push('/dashboard/das');
          }}>
            <FontAwesomeIcon icon={faUserTie} /> Manage Clients 
          </Link>
          <Link href="#">
            <FontAwesomeIcon icon={faCog} /> Settings
          </Link>
          <Link href="#" onClick={(e) => {
            e.preventDefault();
            this.props.logoutUser();
          }}>
            <FontAwesomeIcon icon={faSignOutAlt} /> Logout
          </Link>
        </div>
      </React.Fragment>
    )
  }
}

const mapStateToProps = (state) => ({
  users: state.users
});

const mapDispatchToProps = (dispatch) => {
  return {
    logoutUser:  () => dispatch(logoutUser()), 
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AdminMenus))
